import React, { Component, PropTypes } from 'react';
import { connect } from 'react-redux';
import Helmet from 'react-helmet';


export default class Login extends Component{
	constructor(props){
		super(props);
		this.state={
			email:'',
			password:'',
			message:''
			
		}
	}
	handleSignUpInput(e){
		var name= e.target.name;
		var value= e.target.value;
		this.setState({[name]:value})
	}

	handleSignUp(e){
		e.preventDefault();
		var signup_data= this.state;
		console.log("signup",signup_data);

		// to send data to server
		fetch("http://192.168.20.168:4000/api/register",{
			method:"POST",
			headers:{ 'Accept':'application/json , text/plain, */*',
						'Content-type':'application/json' },
			body:JSON.stringify(signup_data)
		}).then((res)=> res.json())
		  .then(res=>{
		  		if(typeof res=== "object")
		  		{
		  			
		  			var data= res.message;
		  			this.setState({message:data})
		  			
		  			
		  		}
		  })
		  .catch((err)=> console.log(err))
	}

	render(){
		const {email, password}= this.state;
		const isEnabled= email.length > 0 && password.length == 4;
		var d
		console.log("ur response",this.data)
		if(this.state.message){
			d= <span className="errorMessage">{this.state.message}</span>
		}
		return(
			
					<div className="signUp_section">
						<div className="signUp_img">
							<span className="signup_text">Sign Up</span>
							<img src="../assets/images/login/signup.png" alt="signUp_img"/>
						</div>
						<div className="signUp_input">
							<form onSubmit={this.handleSignUp.bind(this)}>
								<div className="form-group">
									<input 
										type="email" 
										name="email" 
										className="user_inputs_signup form-control" 
										placeholder="Email" 
										value={this.state.email} 
										onChange={this.handleSignUpInput.bind(this)}/>
								</div>
								<div className="form-group">
									<input 
										type="password" 
										name="password" 
										className="user_inputs_signup form-control" 
										placeholder="Password" 
										value={this.state.password} 
										onChange={this.handleSignUpInput.bind(this)}/>
								</div>
								<div className="form-group">
									<span><a>Terms and Conditions</a></span>
								</div>
								
								<div className="form-group text-right">
									<input 
										type="submit" 
										name="SignUp" 
										value="Sign Up" 
										data-toggle="tooltip" data-placement="top" title="SignUp"
										className={isEnabled==true?"submit_button":"IMDisabled"}
										disabled={!isEnabled}/>
								</div>
							</form>
							{d}
						</div>
					</div>	
			
			
		)
	}
}